<template>
    <div class="h-full">
        <div class="panel-nav">
            <el-breadcrumb separator="/" class="m-l-lg">
                <el-breadcrumb-item>
                {{$t('collage.nav')}}
                </el-breadcrumb-item>
            </el-breadcrumb>
            <el-button class="btn btn-gradient m-r-lg" @click="openAddDialog('create')">
                {{$t('collage.create')}}
            </el-button>
        </div>
        <div class="panel-body-list clearfix">
            <list-table
                :data="listData"
                :fields="fields"
                :operators="operators"
                class="wrapper-md"
                :operators-title="$t('operate.title')">
                <div slot="btns-bottom" slot-scope="slotData">
                    <el-button class="btn btn-manage" @click="removeItems(slotData)">{{$t('operate.remove')}}</el-button>
                </div>
            </list-table>
        </div>
        <edit-dialog
            :visible="addForm.visible"
            :data="addForm.data"
            :title="addForm.title"
            :fields="addForm.fields"
            class="dialog-md"
            @close="addForm.cancel"
            @confirm="addForm.confirm"
            ></edit-dialog>
        <tip-dialog
            :visible="deleteData.visible"
            :message="deleteData.message"
            :title="deleteData.title"
            @close="deleteData.close"
            @confirm="deleteData.confirm"
            :type="deleteData.type"
            ></tip-dialog>
    </div>
</template>
<script>
    import listTable from '../table/table'
    import editDialog from '../common/editDialog'
    import tipDialog from '../common/tipDialog'
    import { getSelectedRows } from '@/assets/js/untils'
    import _ from 'lodash'

    export default {
        created() {
            this.getCollageList()
        },
        data() {
            return {
                listData: [],
                fields: [{
                    name: 'name',
                    showName: this.$t('collage.name')
                }],
                operators: [{
                    name: this.$t('operate.edit'),
                    handler: this.edit
                }, {
                    name: this.$t('operate.remove'),
                    handler: this.remove
                }],
                addForm: {
                    title: this.$t('collage.create'),
                    visible: false,
                    mode: 'create',
                    data: {
                        name: ''
                    },
                    fields: [{
                        name: 'name',
                        showName: this.$t('collage.name'),
                        type: 'text',
                        required: true
                    }],
                    confirm: () => {
                        this.$httpMethods.collage[this.addForm.mode + "Collage"](this.addForm.data).then(res => {
                            this.addForm.visible = false
                            this.addForm.title = this.$t('collage.create')
                            this.addForm.reset()
                        })
                        
                    },
                    cancel: () => {
                        this.addForm.visible = false
                        this.addForm.reset()
                    },
                    reset: () => {
                        this.addForm.title = this.$t('collage.create')
                        this.addForm.data = {
                            name: '' 
                        }
                        this.addForm.mode = 'create'
                    }
                },
                deleteData: {
                    visible: false,
                    title: this.$t('collage.deleteTitle'),
                    message: '',
                    type: 'delete',
                    ids: [],
                    confirm: () => {
                        this.deleteData.visible = false
                        this.$httpMethods.collage.deleteCollage(this.deleteData.ids).then(res => {
                            if(res && res.status === 200) {
                                this.getCollageList()
                                this.$notify({
                                    type: 'success',
                                    title: this.$t('common.success'),
                                    message: this.$t('collage.message.deleteSuccess')
                                })
                            }
                        })
                    },
                    close: () => {
                        this.deleteData.visible = false
                    }
                }
            }
        },
        methods: {
            getCollageList() {
                this.$httpMethods.collage.queryAllCollages().then(res => {
                    if(res && res.status === 200) {
                        this.listData = res.data
                    }
                })
            },
            remove(data) {
                this.deleteData.message = this.$t('collage.message.delete', { name: data.$$src.name })
                this.deleteData.visible = true
                this.deleteData.ids = [data.$$src.id]
            },
            removeItems(data) {
                if(data.tableData && data.tableData.selection) {
                    let ids = []
                    data.tableData.selection.forEach(row => {
                        ids.push(row.$$src.id)
                    })
                    if(ids.length === 1) {
                        this.deleteData.message = this.$t('collage.message.delete', { name: this.selectRows[0].$$src.name })
                    } else {
                        this.deleteData.message = this.$t('collage.message.deleteItems', { length: ids.length })
                    }
                    this.deleteData.visible = true
                    this.deleteData.ids = ids;
                }
            },
            refresh() {
                this.$httpMethods.collage.queryAllCollages().then(res => {
                    if(res && res.status === 200) {
                        this.listData = res.data
                        this.$notify({
                            type: 'success',
                            title: this.$t('common.success'),
                            message: this.$t('collage.message.refresh')
                        })
                    }
                })
            },
            edit(item) {
                console.log(item)
                this.addForm.title = this.$t('collage.edit')
                this.addForm.data = _.clone(item.$$src)
                this.openAddDialog('update')
            },
            openAddDialog(type) {
                this.addForm.mode = type
                this.addForm.visible = true
            }
        },
        created() {
            this.getCollageList()
        },
        components: {
            listTable,
            editDialog,
            tipDialog
        }
    }
</script>
<i18n>
    {
        "zh": {
            "collage": {
                "name": "学院名称",
                "create": "新建学院",
                "edit": "编辑学院",
                "nav": "学院管理",
                "deleteTitle": "确认删除",
                "message": {
                    "delete": "确定要删除 \"{name}\" 学院吗？",
                    "deleteItems": "确定要删除这 \"{length}\" 项吗？",
                    "refresh": "刷新成功",
                    "deleteSuccess": "删除成功"
                }
            },
            "operate": {
                "add": "添加",
                "title": "操作",
                "remove": "删除",
                "edit": "编辑",
                "reset": "重置密码"
            }
        }
    }
</i18n>


